@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:ital@1&display=swap);
.navbar {
    background-color: var(--accent);
    padding: 20px 28px;
    border-radius: 0 0 var(--border-radius-default) var(--border-radius-default);
}

@media (max-width: 801px) {
    #navbar_internal_container {
        flex-direction: column;
    }
    #logo_container {
        justify-content: center!important;
    }
}
@media (max-width: 620px) {
    #navbar_internal_container {
        flex-direction: row;
    }
    #logo_container {
        justify-content: flex-end!important;
    }
}
.galaxy-background {
    border-radius: var(--border-radius-default);
    transition: all 2s;
    -webkit-animation: galaxy-rotate 2s linear infinite;
            animation: galaxy-rotate 2s linear infinite;
}

@-webkit-keyframes galaxy-rotate {
    from {
        background-image: linear-gradient(to bottom right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to bottom left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(180deg, rgba(0, 65, 180, 0.7) 30%, transparent 100%),
        linear-gradient(0deg, rgba(178, 0, 28, 0.7) 50%, transparent 90%);
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    to {
        background-image: linear-gradient(to bottom right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to bottom left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(180deg, rgba(178, 0, 28, 0.7) 30%, transparent 100%),
        linear-gradient(0deg, rgba(0, 65, 180, 0.7) 50%, transparent 100%);
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes galaxy-rotate {
    from {
        background-image: linear-gradient(to bottom right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to bottom left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(180deg, rgba(0, 65, 180, 0.7) 30%, transparent 100%),
        linear-gradient(0deg, rgba(178, 0, 28, 0.7) 50%, transparent 90%);
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    to {
        background-image: linear-gradient(to bottom right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to bottom left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top right, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(to top left, var(--secondary-accent) 30%, transparent 30%),
        linear-gradient(180deg, rgba(178, 0, 28, 0.7) 30%, transparent 100%),
        linear-gradient(0deg, rgba(0, 65, 180, 0.7) 50%, transparent 100%);
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.logo {
    font-size: 45px;
    font-style: italic;
    font-weight: 900;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: progress;
}
.navbar-item {
    padding: 12px 23px;
    background: black;
    color: var(--accent);
    box-shadow: 0 0 13px black;
    transition: all 0.21s, background-color 0.5s, color 0.5s;
    border: 3px solid black;

}
.navbar-item:hover {
    box-shadow: 0 0 35px black;
    background: var(--accent);
    color: black;
}
.navbar-items{}

@media (max-width: 620px) {
    .navbar-items {
        display: none!important;
    }
}
.navbar-item-dropdown {
    display: none;
}
.navbar-item-dropdown:hover .navbar-items{
    display: flex!important;
}
@media (max-width: 620px) {
    .navbar-item-dropdown {
        display: block!important;
    }
}
.start {
    padding: 30px;
}
.title {
    font-size: 30px;
    font-family: Nunito, sans-serif;
    font-weight: bold;
    text-align: center;
    text-transform: capitalize;
    color: var(--accent);
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    justify-content: center;
    margin-bottom: 40px;
}
.title-internal {
    padding-bottom: 5px;
    border-bottom: 4px solid var(--accent);
}
.paragraph {
    width: 90%;
    color: var(--accent);
    text-align: center;
    font-family: Nunito, sans-serif;
    font-size: 15px;
    text-transform: capitalize;
    line-height: 1.7rem;
    letter-spacing: 0.3rem;
    transition: all 0.7s;
}
.quotes-container {
    width: 100%;
    height: 100%;
    justify-content: center;
}

.quotes-internal-container {
    width: 300px;
    height: 100%;
    overflow: hidden;
}

.quote-holder {
    width: 500%;
    height: 100%;
    transition: all 1s;
}

@media (max-width: 620px) {
    .quotes-internal-container {
        width: 100%;
    }
    .quote-holder {
        margin: 10px 0;
    }
}

.quote-box {
    width: 250px;
    background-color: var(--accent);
    border-radius: var(--border-radius-default);
    border: 1px solid var(--accent);
    padding: 20px;
    cursor: default;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: all 0.5s;
}
.quote-body {
    font-family: Nunito, sans-serif;
    font-size: 20px;
    font-style: italic;
    text-transform: capitalize;
    transition: all 0.7s;
}
.quote-box:hover {
    background: none;
}
.quote-box:hover .quote-body {
    color: var(--accent);
}
.quote-box:hover .paragraph {
    color: var(--accent) !important;
}
.change-quote-button {
    background-color: var(--accent);
    color: black;
    border-radius: var(--border-radius-default);
    padding: 15px;
    border: 2px solid var(--accent);
    margin: auto;
    transition: all 0.5s;
}
.change-quote-button:hover {
    background-color: black;
    color: var(--accent);
}
@media (max-width: 620px) {
    .connect-box-holder {
        flex-direction: column;
    }
}
.connect-box {
    background-color: var(--accent);
    border: 2px solid var(--accent);
    border-radius: var(--border-radius-default);
    padding: 15px 25px;
    flex-wrap: wrap;
    transition: all 0.7s;
}

@media (max-width: 620px) {
    .connect-box{
        margin-bottom: 20px!important;
        margin-right: auto!important;
    }
}
.connect-box-text {
    color: black;
}
.img-inside {
    width: 50px;
    height: 50px;
    padding: 10px;
    border-radius: var(--border-radius-default);
}
.connect-box:hover {
    background: none;
}
.connect-box:hover .connect-box-text {
    color: var(--accent);
}
.flex {
    display: flex;
}

.flex-col {
    flex-direction: column;
}

.m-auto {
    margin: auto;
}

.full-height {
    height: 100%;
}
.full-width {
    width: 100%;
}
.half-width {
    width: 50%;
}

.c-font-default {
    font-family: Nunito, sans-serif;
    letter-spacing: 0.25rem;
}

.buttons-text-size {
    font-size: 21px;
}

.cursor-p {
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.space-b {
    justify-content: space-between;
}

.space-e {
    justify-content: space-evenly;
}

.cut-background {
    background-image:
            linear-gradient(5deg, black 5vw, transparent 10%),
            linear-gradient(-10deg, black 20%, transparent 10%);
}

.black {
    background-color: black;
}

.content-container {
    padding: 50px;
}
